<template>
  <div class="knowledge-keywords">
    <!-- 顶部统计与操作区 -->
    <div class="top-bar">
      <div class="stats">
        <div class="stat-item">
          <div class="stat-label">全部数量</div>
          <div class="stat-value">{{ totalCount }}</div>
        </div>
        <div class="stat-item" v-for="cat in categoryStats" :key="cat.name">
          <div class="stat-label">{{ cat.name }}</div>
          <div class="stat-value">{{ cat.count }}</div>
        </div>
      </div>
      <div class="actions">
        <select v-model="selectedCategory" class="select">
          <option value="">全部分类</option>
          <option v-for="cat in allCategories" :key="cat" :value="cat">{{ cat }}</option>
        </select>
        <input v-model="searchText" class="search" placeholder="搜索关键词或知识内容..." />
        <button class="btn-industry" @click="subscribeIndustryPack">
          <i class="fas fa-box mr-2"></i>订阅行业包
        </button>
      </div>
    </div>

    <!-- 知识卡片分组区 -->
    <div class="card-list">
      <div v-for="(group, idx) in filteredGroups" :key="group.category" class="card-group">
        <div class="card-header" @click="toggleGroup(idx)">
          <div>
            <span class="group-title">{{ group.category }}</span>
            <span class="industry-tag">{{ group.industryTag }}</span>
            <span class="hit-count">近7日命中: {{ group.questions.reduce((sum, q) => sum + (q.hit_count_7d || 0), 0) }}</span>
          </div>
          <button class="btn-add" @click.stop="addKnowledge(idx)"><i class="fas fa-plus"></i> 添加知识</button>
          <button class="btn-toggle">
            <i :class="['fas', group.expanded ? 'fa-chevron-up' : 'fa-chevron-down']"></i>
          </button>
        </div>
        <transition name="fade">
        <div v-show="group.expanded" class="card-body">
          <div v-for="(qa, qidx) in group.questions" :key="qidx" class="qa-item">
            <div class="qa-header">
              <div class="qa-question">
                <span class="qa-label">客户常问：</span>
                <span class="qa-example">{{ qa.example || '暂无示例' }}</span>
                <span class="qa-keywords">
                  <span v-for="(kw, kidx) in qa.keywords" :key="kidx" class="keyword">{{ kw }}</span>
                  <button class="btn-edit" @click="editKeywords(idx, qidx)">编辑关键词</button>
                </span>
              </div>
              <button class="btn-delete" @click="deleteKnowledge(idx, qidx)"><i class="fas fa-trash"></i></button>
            </div>
            <div class="qa-answers">
              <div v-for="(ans, aidx) in qa.answers" :key="aidx" class="answer-item">
                <span class="answer-label">知识内容：</span>
                <span class="answer-text">{{ ans }}</span>
                <button class="btn-edit" @click="editAnswer(idx, qidx, aidx)">配置</button>
                <button class="btn-delete" @click="deleteAnswer(idx, qidx, aidx)"><i class="fas fa-trash"></i></button>
              </div>
              <button class="btn-add" @click="addAnswer(idx, qidx)"><i class="fas fa-plus"></i> 添加知识</button>
            </div>
          </div>
        </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'KnowledgeKeywords',
  data() {
    return {
      // mock 数据结构
      groups: [
        {
          category: '发票问题',
          industryTag: '行业通用',
          expanded: true,
          questions: [
            {
              example: '怎么开发票？',
              keywords: ['发票', '开发票', '电子发票'],
              answers: ['【可以开发票的哦~】'],
              hit_count_7d: 3
            }
          ]
        },
        {
          category: '退换货类',
          industryTag: '行业通用',
          expanded: false,
          questions: [
            {
              example: '我已经申请退货了还可以退款吗？',
              keywords: ['退货', '退款', '取消订单'],
              answers: ['退货后会自动退款，耐心等待到账即可。'],
              hit_count_7d: 5
            }
          ]
        }
      ],
      selectedCategory: '',
      searchText: ''
    }
  },
  computed: {
    totalCount() {
      return this.groups.reduce((sum, g) => sum + g.questions.length, 0)
    },
    allCategories() {
      return this.groups.map(g => g.category)
    },
    categoryStats() {
      return this.groups.map(g => ({ name: g.category, count: g.questions.length }))
    },
    filteredGroups() {
      let groups = this.groups
      if (this.selectedCategory) {
        groups = groups.filter(g => g.category === this.selectedCategory)
      }
      if (this.searchText) {
        const kw = this.searchText.toLowerCase()
        groups = groups.map(g => ({
          ...g,
          questions: g.questions.filter(q =>
            q.keywords.some(k => k.includes(kw)) ||
            q.answers.some(a => a.includes(kw))
          )
        })).filter(g => g.questions.length > 0)
      }
      return groups
    }
  },
  methods: {
    toggleGroup(idx) {
      this.groups[idx].expanded = !this.groups[idx].expanded
    },
    subscribeIndustryPack() {
      this.$message && this.$message.success('订阅行业包功能待接入')
    },
    addKnowledge() {
      // TODO: 打开弹窗新增知识
      alert('添加知识（弹窗开发中）')
    },
    editKeywords() {
      // TODO: 编辑关键词弹窗
      alert('编辑关键词（弹窗开发中）')
    },
    deleteKnowledge(idx, qidx) {
      this.groups[idx].questions.splice(qidx, 1)
    },
    addAnswer() {
      // TODO: 新增答案弹窗
      alert('添加答案（弹窗开发中）')
    },
    editAnswer() {
      // TODO: 编辑答案弹窗
      alert('编辑答案（弹窗开发中）')
    },
    deleteAnswer(idx, qidx, aidx) {
      this.groups[idx].questions[qidx].answers.splice(aidx, 1)
    }
  }
}
</script>

<style scoped>
